@import (less) 'color.less';

.customer-section-title {
  font-size: 1em;
  font-weight: 700;
  padding-top: 24px;
}

.customer-section-info {
  font-size: 1em;
  padding-top: 6px;
}

.sign-icon {
  font-size: 1.9em;
}

.customer-circle-info {
  width: 40px;
  height: 40px;
  border: 1px solid @light_gray;
  border-radius: 40px;
  background: @white;
  position: absolute;
  top: -20px;
  text-align: center;
  font-weight: 700;
  padding-top: 8px;
  color: @light_green;
}
